import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { BaseRequestOptions, HttpModule } from '@angular/http';
import { MockBackend } from '@angular/http/testing';

import { AuthRoutingModule } from './auth-routing.routing';
import { AuthComponent } from './auth.component';
import { AlertComponent } from './_directives/alert.component';
import { LogoutComponent } from './logout/logout.component';
import { AuthGuard } from './_guards/auth.guard';
import { AlertService } from './_services/alert.service';
import { AuthenticationService } from './_services/authentication.service';
import { UserService } from './_services/user.service';
import { ReportsService } from './_services/reports.service';
import { CustomerService } from './_services/customer.service';
import { SupportService } from './_services/support.service';
import { MenuService } from './_services/menu.service';
import { BrandService } from './_services/brand.service';
import { ProductService } from './_services/product.service';

import { CategoryService } from './_services/category.service';
import { SubCategoryService } from './_services/subCategory.service';
import { fakeBackendProvider } from './_helpers/index';
import { BannerService } from './_services/banner.service';
import { OrderService } from './_services/order.service';
import { ReviewsService } from './_services/reviews.service';
import { DropshipService } from './_services/dropship.service';
import { SubscriptionService } from './_services/subscription.service';
import { WithdrawService } from './_services/withdraw.service';
import { PermissionService } from './_services/permission.service';


@NgModule({
    declarations: [
        AuthComponent,
        AlertComponent,
        LogoutComponent,
    ],
    imports: [
        CommonModule,
        FormsModule,
        HttpModule,
        AuthRoutingModule,
    ],
    providers: [
        AuthGuard,
        AlertService,
        AuthenticationService,
        UserService,
        BrandService,
        ProductService,
        SubscriptionService,
        WithdrawService,
        // api backend simulation
        fakeBackendProvider,
        MockBackend,
        BaseRequestOptions,
        ReportsService,
        CustomerService,
        ReviewsService,
        DropshipService,
        SupportService,
        CategoryService,
        MenuService,
        SubCategoryService,
        BannerService,
        OrderService,
        PermissionService
    ],
    entryComponents: [AlertComponent],
})

export class AuthModule {
}
