import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { SubCategory } from "../_models/index";

@Injectable()
export class SubCategoryService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'subCategory/getall', this.jwt()).map((response: Response) => response.json());
    }
    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'subCategory/info/' + id, this.jwt()).map((response: Response) => response.json());
    }
    getByName(name: any) {
        return this.http.get(this.baseUrl._baseUrl + 'subCategory/name-search/' + name, this.jwt()).map((response: Response) => response.json());
    }
    subCategoryDelete(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'subCategory/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }
    updateStatus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/status', id, this.jwt()).map((response: Response) => response.json());
    }
    updateSubCategory(subCategory: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/updateSubCategory', JSON.stringify(subCategory), this.jwt()).map((response: Response) => response.json());
    }
    createSubCategory(subCategory: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/addSubCategory', JSON.stringify(subCategory), this.jwt()).map((response: Response) => response.json());
    }


    getCategory(menu_id: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/getCategory', JSON.stringify(menu_id), this.jwt()).map((response: Response) => response.json());
    }

    getSubCategory(catgeory_id: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/getSubCategory', catgeory_id, this.jwt()).map((response: Response) => response.json());
    }
    getSubCategoryAss(catgeory_id: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/getSubCategory', catgeory_id, this.jwt()).map((response: Response) => response.json());
    }

    searching(subCategory: any) {
        return this.http.post(this.baseUrl._baseUrl + 'subCategory/search', JSON.stringify(subCategory), this.jwt()).map((response: Response) => response.json());
    }



    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
