import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Menu } from "../_models/index";

@Injectable()
export class MenuService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'menu/menulist', this.jwt()).map((response: Response) => response.json());
    }

    getProductType() {
        return this.http.get(this.baseUrl._baseUrl + 'menu/product-type', this.jwt()).map((response: Response) => response.json());
    }

    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'menu/info/' + id, this.jwt()).map((response: Response) => response.json());
    }
    getByName(name: any) {
        return this.http.get(this.baseUrl._baseUrl + 'menu/name-search/' + name, this.jwt()).map((response: Response) => response.json());
    }



    /* getById(id: number) {
         return this.http.post(this.baseUrl._baseUrl + 'menu/info', JSON.stringify(id), this.jwt()).map((response: Response) => response.json());
     }*/

    menuDelete(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'menu/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }

    searching(menu: any) {
        return this.http.post(this.baseUrl._baseUrl + 'menu/search', JSON.stringify(menu), this.jwt()).map((response: Response) => response.json());
    }

    createMenu(menu: any) {
        return this.http.post(this.baseUrl._baseUrl + 'menu/addMenu', JSON.stringify(menu), this.jwt()).map((response: Response) => response.json());
    }


    updateMenu(menu: any) {
        return this.http.post(this.baseUrl._baseUrl + 'menu/updateMenu', JSON.stringify(menu), this.jwt()).map((response: Response) => response.json());
    }

    updateStatus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'menu/status', id, this.jwt()).map((response: Response) => response.json());
    }

    dashboardService(user: any) {
        return this.http.post(this.baseUrl._baseUrl + 'menu/dashboard-service', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());

    }
    // private helper methods


    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
