import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';

@Injectable()
export class PermissionService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAllUserGroup() {
        return this.http.get(this.baseUrl._baseUrl + 'user-group/getall', this.jwt()).map((response: Response) => response.json());
    }
    getUserGroupById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'user-group/get-user-group-by-id/' + id, this.jwt()).map((response: Response) => response.json());
    }
    getUserPermissionById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'user-group/permission/' + id, this.jwt()).map((response: Response) => response.json());
    }
    updateUserPermision(userPermission: any) {
        return this.http.post(this.baseUrl._baseUrl + 'user-group/permission/', JSON.stringify(userPermission), this.jwt()).map((response: Response) => response.json());
    }


    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
