import { Component, OnInit, Input } from '@angular/core';
import { DataService } from '../../../../../../_services/data.service';
import { ProductService } from '../../../../../../auth/_services/product.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";

@Component({

    selector: 'edit-shareable-product',
    templateUrl: "edit-shareable-product.component.html",
})

export class EditShareableProductComponent implements OnInit {


    constructor(private _dataService: DataService, private _productService: ProductService, private toastr: ToastrService, private router: Router) { }

    public selectedProductData: any;
    public isReadOnly = true;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_edit != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this._dataService.getsideBarData().subscribe(
            data => {

                this.selectedProductData = data;
            }
        );

    }

    public editMode() {
        this.isReadOnly = false;
    }
    public closeSidebar() {
        this.isReadOnly = true;
        this._dataService.setloaderState(false);
    }

    public UpdateVendor() {
        // console.log(this.selectedVendorData);
        // this._productService.update(this.selectedVendorData);
    }

} 
