import { Injectable } from "@angular/core";
import { Headers, Http, Response, RequestOptions } from "@angular/http";
import "rxjs/add/operator/map";
import { ScriptLoaderService } from "../../_services/script-loader.service";

@Injectable()
export class AuthenticationService {

    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    login(email: string, password: string) {
        //let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
        //let options = { headers: headers };
        console.log('reached');
        return this.http.post(this.baseUrl._baseUrl + 'login', JSON.stringify({ email: email, password: password }))
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let user = response.json();
                console.log('user');
                console.log(user);
                user.token = user.success.token;
                if (user && user.token) {

                    // store user details and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem('currentUser', JSON.stringify(user));
                }
            });
    }

    logout() {
        let input: any;
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));

        input = { 'user_id': currentUser.success.id, 'is_admin': false, 'own': false };
        this.http.post(this.baseUrl._baseUrl + 'logout', input, this.jwt()).map((response: Response) => response.json());
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
        localStorage.removeItem('subCategory');
        localStorage.removeItem('productType');
        localStorage.removeItem('menu');

    }

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
