import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Category } from "../_models/index";

@Injectable()
export class CategoryService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'category/categorylist', this.jwt()).map((response: Response) => response.json());
    }

    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'category/info/' + id, this.jwt()).map((response: Response) => response.json());
    }
    getByName(name: any) {
        return this.http.get(this.baseUrl._baseUrl + 'category/name-search/' + name, this.jwt()).map((response: Response) => response.json());
    }
    updateCategory(category: any) {
        return this.http.post(this.baseUrl._baseUrl + 'category/updateCat', JSON.stringify(category), this.jwt()).map((response: Response) => response.json());
    }

    categoryDelete(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'category/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }

    createCategory(category: any) {
        return this.http.post(this.baseUrl._baseUrl + 'category/addCategory', JSON.stringify(category), this.jwt()).map((response: Response) => response.json());
    }
    updateStatus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'category/status', id, this.jwt()).map((response: Response) => response.json());
    }
    searching(category: any) {
        return this.http.post(this.baseUrl._baseUrl + 'category/search', JSON.stringify(category), this.jwt()).map((response: Response) => response.json());

    }

    categoryListAss() {
        return this.http.get(this.baseUrl._baseUrl + 'category/categoryListAss', this.jwt()).map((response: Response) => response.json());

    }




    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
