import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Reviews } from "../_models/index";

@Injectable()
export class ReviewsService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'reviews/getall', this.jwt()).map((response: Response) => response.json());
    }
    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'reviews/info/' + id, this.jwt()).map((response: Response) => response.json());
    }

    del_reviews(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'reviews/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }

    searching(user) {
        return this.http.post(this.baseUrl._baseUrl + 'reviews/search', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }
    updateStatus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'reviews/status', id, this.jwt()).map((response: Response) => response.json());

    }
    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
