import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { Order } from "../_models/index";

@Injectable()
export class OrderService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll(user_id: any) {
        return this.http.post(this.baseUrl._baseUrl + 'order/order-list', JSON.stringify(user_id), this.jwt()).map((response: Response) => response.json());
    }
    getById(order_no: any) {
        return this.http.post(this.baseUrl._baseUrl + 'order/info/', JSON.stringify(order_no), this.jwt()).map((response: Response) => response.json());
    }

    shippingStatus(status: any) {
        return this.http.post(this.baseUrl._baseUrl + 'order/shipping-status/', JSON.stringify(status), this.jwt()).map((response: Response) => response.json());
    }

    filter(filter: any) {
        return this.http.post(this.baseUrl._baseUrl + 'order/order-filter/', JSON.stringify(filter), this.jwt()).map((response: Response) => response.json());
    }

    updateStatus(cart_data: any) {
        return this.http.post(this.baseUrl._baseUrl + 'order/order-update/', JSON.stringify(cart_data), this.jwt()).map((response: Response) => response.json());
    }

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}