import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
//import { dropship } from "../_models/index";

@Injectable()
export class DropshipService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    getAll() {
        return this.http.get(this.baseUrl._baseUrl + 'dropship/getall', this.jwt()).map((response: Response) => response.json());
    }
    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'dropship/info/' + id, this.jwt()).map((response: Response) => response.json());
    }

    del_dropship(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'dropship/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }

    del_dropship_product(del_data: any) {
        return this.http.post(this.baseUrl._baseUrl + 'dropship/product_delete/', JSON.stringify(del_data), this.jwt()).map((response: Response) => response.json());
    }

    searching(user) {
        return this.http.post(this.baseUrl._baseUrl + 'dropship/search', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }
    updateStatus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + 'dropship/status', id, this.jwt()).map((response: Response) => response.json());

    }

    filter(product: any) {
        return this.http.post(this.baseUrl._baseUrl + 'dropship/search', JSON.stringify(product), this.jwt()).map((response: Response) => response.json());
    }

    createCollection(product: any) {
        return this.http.post(this.baseUrl._baseUrl + 'dropship/addCollection', JSON.stringify(product), this.jwt()).map((response: Response) => response.json());
    }

    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
