import { Component, HostBinding, Input } from '@angular/core';
import { SideBarService } from './side-bar.service';
import { DataService } from '../../../_services/data.service';
import { VendorEditComponent } from '../../pages/default/vendor/vendor-edit/vendor-edit.component';
import { EditShareableProductComponent } from '../../pages/default/product/shareable-products/edit-shareable-product/edit-shareable-product.component';
@Component({
    selector: 'app-side-bar',
    templateUrl: './side-bar.component.html',
    styleUrls: ['./side-bar.component.css']
})
export class SideBarComponent {

    @HostBinding('class.is-open')
    isOpen = false;
    public activeSelector: string;
    constructor(
        private sideBarService: SideBarService,
        private _dataService: DataService
    ) { }

    ngOnInit() {
        this._dataService.getloaderState().subscribe(
            data => {
                this.isOpen = data;
            }
        );

        this._dataService.getactiveMenu().subscribe(
            data => {
                this.activeSelector = data;
            }
        );

        this.sideBarService.change.subscribe(isOpen => {
            this.isOpen = isOpen;
        });
    }



}
