import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';

@Injectable()
export class DataService {


    data = {
        leftSidebar: true,
        header: true,
        rightSidebar: true
    };
    loaderState = false;
    public loaderState$ = new BehaviorSubject(this.loaderState);
    public sideBarData = new BehaviorSubject<any>({});
    public activeMenu = new BehaviorSubject<any>('');
    public productState = new BehaviorSubject<any>('1');
    public notification = new BehaviorSubject<number>(0);
    constructor() { }

    setloaderState(state: boolean): any {
        this.loaderState$.next(state);
    }

    getloaderState(): Observable<any> {
        return this.loaderState$.asObservable();
    }

    setsideBarData(details: any): any {
        this.sideBarData.next(details);
    }

    getsideBarData(): Observable<any> {
        return this.sideBarData.asObservable();
    }

    setactiveMenu(details: any): any {
        this.activeMenu.next(details);
    }

    getactiveMenu(): Observable<any> {
        return this.activeMenu.asObservable();
    }

    setProductState(details: any): any {
        this.productState.next(details);
    }

    getProductState(): Observable<any> {
        return this.productState.asObservable();
    }

    setNotification(details: any): any {
        this.notification.next(details);
    }

    getNotification(): Observable<any> {
        return this.notification.asObservable();
    }

}
