import { Injectable } from "@angular/core";
import { Headers, Http, RequestOptions, Response } from "@angular/http";
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { User } from "../_models/index";

@Injectable()
export class UserService {
    constructor(private http: Http, private baseUrl: ScriptLoaderService) {
    }

    verify() {
        return this.http.get('/api/verify', this.jwt()).map((response: Response) => response.json());
    }

    forgotPassword(email: string) {
        return this.http.post('/api/forgot-password', JSON.stringify({ email }), this.jwt()).map((response: Response) => response.json());
    }
    getAll() {
        return this.http.get(this.baseUrl._baseUrl + localStorage.getItem('section') + '/getall', this.jwt()).map((response: Response) => response.json());
    }
    getUserRoleAll() {
        return this.http.get(this.baseUrl._baseUrl + 'vendor/get-user-role-all', this.jwt()).map((response: Response) => response.json());
    }

    getById(id: number) {
        return this.http.get(this.baseUrl._baseUrl + 'vendor/info/' + id, this.jwt()).map((response: Response) => response.json());
    }
    create(user) {
        return this.http.post('/api/users', user, this.jwt()).map((response: Response) => response.json());
    }
    update(user: User) {
        return this.http.post(this.baseUrl._baseUrl + localStorage.getItem('section') + '/update', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }
    update_username(user: User) {
        return this.http.post(this.baseUrl._baseUrl + localStorage.getItem('section') + '/updateuser', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }
    update_password(user: User) {
        return this.http.post(this.baseUrl._baseUrl + localStorage.getItem('section') + '/updatepass', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }
    del_vendor(id: number) {
        return this.http.get(this.baseUrl._baseUrl + localStorage.getItem('section') + '/delete/' + id, this.jwt()).map((response: Response) => response.json());
    }
    logout_vendor(input: any) {
        return this.http.post(this.baseUrl._baseUrl + 'logout', input, this.jwt()).map((response: Response) => response.json());
    }

    searching(user) {
        return this.http.post(this.baseUrl._baseUrl + localStorage.getItem('section') + '/search', JSON.stringify(user), this.jwt()).map((response: Response) => response.json());
    }

    updateStaus(id: number) {
        return this.http.post(this.baseUrl._baseUrl + localStorage.getItem('section') + '/status', id, this.jwt()).map((response: Response) => response.json());
    }

    islogin() {
        return this.http.get(this.baseUrl._baseUrl + 'isLogin', this.jwt()).map((response: Response) => response.json());
    }




    // private helper methods

    private jwt() {
        // create authorization header with jwt token
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if (currentUser && currentUser.token) {
            let headers = new Headers({ 'Authorization': 'Bearer ' + currentUser.token });
            return new RequestOptions({ headers: headers });
        }
    }
}
